import axios from 'axios'
/* eslint-disable */
const CONFIG = {
  URL: `https://api.airtable.com/v0/app86aKEkJ23iE5c9/DogDogORC?api_key=${process.env.VUE_APP_AIRTABLE_API_KEY}`,
  readLocalDB: process.env.VUE_APP_READ_LOCAL_DB
}
console.log(process.env)
const DogAPI = {
  async getAll () {
    console.log(CONFIG)
    if (CONFIG.readLocalDB) {
      return require('./db.json').records.map(records => {
        return records.fields
      })
    } else {
      const { data: res } = await axios.get(CONFIG.URL)
      return res.records.map(records => {
        return records.fields
      })
    }
  }
}

export default DogAPI