<template>
  <v-layout
    row
    wrap
    class="container"
  >
    <v-flex xs12>
      <div class="display-1 font-weight-medium"> <span class="display-3 font-weight-bold font-italic"> {{exactResult.length}} </span> exact match found </div>
    </v-flex>
    <v-flex
      xs12
      sm12
      md6
      lg6
      class="result-card"
      v-for="item in exactResult"
      :key="`word-${item.index}`"
    >
      <v-card>
        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0 font-weight-medium text-capitalize">{{item.keyword}}</h3>
            <div>
              <div class="alert">
                <v-chip
                  v-if="item.result['Alert Level'] && item.result['Alert Level'] == 'GREEN'"
                  color="green"
                  text-color="white"
                >
                  {{item.result['Alert Level']}}
                </v-chip>
                <v-chip
                  v-if="item.result['Alert Level'] && item.result['Alert Level'].startsWith('Red')"
                  color="red"
                  text-color="white"
                >
                  {{item.result['Alert Level']}}
                </v-chip>
              </div>
              <div
                class="description"
                v-if="item.result.Name"
              >
                {{item.result.Name}}
              </div>
              <div
                class="description"
                v-if="item.result.description"
              >
                {{item.result.description}}
              </div>

              <div>
                Keywords:
                <v-chip
                  outline
                  v-for="key in getKeywords(item.result.keywords)"
                  :key="`exact-${key}`"
                  color="orange"
                >{{key}}</v-chip>
              </div>
            </div>
          </div>
        </v-card-title>
      </v-card>
    </v-flex>
    <v-flex xs12>
      <div class="display-1 font-weight-medium"> <span class="display-3 font-weight-bold font-italic"> {{fuseResult.length}} </span> possible found ...</div>
    </v-flex>
    <v-flex
      xs12
      sm12
      md6
      lg6
      class="result-card"
      v-for="(item, index) in fuseResult"
      :key="`fword-${index}-${item.index}`"
    >
      <v-card>
        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0 font-weight-medium text-capitalize">{{item.keyword}} </h3>
            <div class="match">
              From Image we see {{item.imageWord.description}} this have {{ getPercent(item.result[0].score) }} match up to {{item.keyword}}
            </div>
            <div>

              <div class="alert">
                <v-chip
                  v-if="item.result[0].item['Alert Level'] && item.result[0].item['Alert Level'] == 'GREEN'"
                  color="green"
                  text-color="white"
                >
                  {{item.result[0].item['Alert Level']}}
                </v-chip>
                <v-chip
                  v-if="item.result[0].item['Alert Level'] && item.result[0].item['Alert Level'].startsWith('Red')"
                  color="red"
                  text-color="white"
                >
                  {{item.result[0].item['Alert Level']}}
                </v-chip>
              </div>
              <div
                class="name"
                v-if="item.result[0].item.Name"
              >
                {{item.result[0].item.Name}}
              </div>
              <div
                class="description"
                v-if="item.result[0].item.description"
              >
                {{item.result[0].item.description}}
              </div>
            </div>
          </div>
        </v-card-title>
        <v-card-actions>
          <div>
            Keywords:
            <v-chip
              outline
              v-for="key in getKeywords(item.result[0].item.keywords)"
              :key="`fx-${key}`"
              color="orange"
            >{{key}}</v-chip>
          </div>
        </v-card-actions>
      </v-card>
    </v-flex>

  </v-layout>
</template>
<script>
import API from '@/api'
import Fuse from 'fuse.js'
/* eslint-disable */
export default {
  props: ['report'],
  data () {
    return {
      DB: [],
      keys: ['Syntax', 'Name', 'keywords', 'E Number / Code'],
      fuse: null,
      imageFuse: null,
      fuseReport: null,
      exactResult: [],
      fuseResult: []
    }
  },
  components: {
  },
  async mounted () {
    console.log(API)
    this.DB = await API.getAll()
    this.startAnalysis()
  },
  computed: {
  },
  watch: {
    report: function (newVal, oldVal) { // watch it
      console.log('Prop changed: ', newVal, ' | was: ', oldVal)
      this.startAnalysis()
    }
  },
  methods: {
    startAnalysis () {
      this.fuseResult = []
      this.exactResult = []
      const options = {
        shouldSort: true,
        threshold: 0.2,
        includeScore: true,
        keys: this.keys
      }
      this.fuse = new Fuse(this.DB, options)
      var doptions = {
        shouldSort: true,
        tokenize: true,
        matchAllTokens: true,
        findAllMatches: true,
        includeScore: true,
        threshold: 0.6,
        location: 0,
        distance: 100,
        maxPatternLength: 32,
        minMatchCharLength: 1,
        keys: [
          "text"
        ]
      }
      if (this.report.fullTextAnnotation) {
        this.imageFuse = new Fuse([this.report.fullTextAnnotation.text], doptions)
        this.fuseSearchReport(this.report.textAnnotations)
        this.searchByDB(this.report.fullTextAnnotation) //loop db serach keyword to search in full text report
      }
    },
    getKeywords (keywords) {
      console.log(keywords.split(','))
      return keywords.split(',')
    },
    getPercent (score) {
      return `${(1 - score) * 100} %`
    },
    searchByDB (report) {
      let keywords = []
      let searchIndex = {}
      this.DB.forEach((record, index) => {
        if (record.keywords) {
          const splited = record.keywords.split(',').map(word => {
            return word.trim()
          })
          keywords = [...keywords, ...splited]
          splited.forEach(x => {
            searchIndex[x] = index
          })
        }
      })
      console.log(searchIndex)
      keywords.forEach(keyword => {
        if (report.text.toLowerCase().indexOf(keyword.toLowerCase()) > -1) {
          console.log(`found ${keyword}`)
          const index = searchIndex[keyword]
          this.exactResult.push({
            index,
            keyword,
            result: this.DB[index],
          })
        }
        const result = this.imageFuse.search(keyword)
        console.log(`SSSsearch : ${keyword}`)
        if (result[0] && result[0].score < 0.5) {
          console.log(`FOUND SSSsearch : ${keyword}`)
          console.log(result)
          const index = searchIndex[keyword]
          // this.fuseResult.push({
          //   index,
          //   keyword,
          //   result: this.DB[index],
          // })
          console.log(index)
        }
      })
    },
    fuseSearchReport (report) {
      // this method is using each report words to search in db 
      const combineWord = ['vitamin']
      report.filter((word, index) => {
        let keyword = word.description
        if (keyword.length > 3 && keyword.length < 50) {
          if (combineWord.indexOf(keyword.toLowerCase()) > -1) {
            keyword = `${keyword} ${report[index + 1].description}`
          }
          const result = this.searchKeyword(keyword, index)
          if (result) {
            console.log(`done search ${index}: ${keyword}`)
            this.fuseResult.push({
              index,
              keyword,
              result,
              imageWord: word,
            })
          }
        }
      })
    },
    searchKeyword (keyword) {
      const result = this.fuse.search(keyword)
      console.log(`search : ${keyword}`)
      if (result.length > 0) {
        console.log(result)
        return result
      }
    }
  }
}
</script>

<style scoped>
.result-card {
  padding: 4px;
  min-height: 300px;
}
</style>