<template>
  <div>
    <v-layout
      row
      wrap
      align-center
      justify-center
    >
      <v-flex xs12>
        <v-card
          flat
          height="720"
        >
          <video
            autoplay
            muted
            playsinline
            ref="video"
            id="video"
          ></video>
          <canvas
            height=100%
            width=100%
            ref="canvas"
            id="canvas"
          ></canvas>
          <v-card-title primary-title>

          </v-card-title>

          <v-card-actions
            class="videoActions"
            align-content-center
          >
            <div>
              <v-btn
                class="shutter"
                color="white"
                flat
                outline
                fab
                id="snap"
                @click="capture"
              >
                <v-icon large>camera</v-icon>
              </v-btn>
            </div>
          </v-card-actions>
          <v-spacer></v-spacer>
          <div class="top-right">
            <v-switch
              v-model="frontCam"
              :append-icon="`camera_${frontCam ? 'rear' : 'front'}`"
            ></v-switch>
          </div>
        </v-card>
      </v-flex>
      <v-flex
        class="capture"
        xs3
        v-for="(c, i) in captures"
        :key="`id_${i}_${c.img}`"
      >
        <img
          :class="currentIndex == i ? 'hightlight' : ''"
          :src="c.image"
          @click="pickReport(i)"
          :alt="`${fileSize(c.image)} kb`"
          width="100%"
        />
      </v-flex>
    </v-layout>
    <ImageResult
      v-show="Object.keys(imgReport).length > 0"
      :report="imgReport"
    ></ImageResult>
  </div>
</template>

<script>
// import IMGFB from '@/api/imageSample.json'
import ImageResult from '@/components/ImageResult'

export default {
  components: {
    ImageResult
  },
  data () {
    return {
      captures: [],
      imgReport: [],
      frontCam: true,
      imageCapture: {},
      DB: {},
      currentIndex: 0,
    }
  },
  async mounted () {
    this.openCam()
  },
  watch: {
    frontCam: function () {
      this.openCam()
    }
  },
  computed: {
    camContraints () {
      return {
        video: {
          facingMode: (this.frontCam ? 'user' : 'environment')
        },
        audio: false
      }
    }
  },
  methods: {
    hasGetUserMedia () {
      return !!(navigator.mediaDevices &&
        navigator.mediaDevices.getUserMedia);
    },
    pickReport (i) {
      this.imgReport = this.captures[i].imgReport
      this.currentIndex = i
    },
    openCam () {
      this.video = this.$refs.video
      if (!this.hasGetUserMedia()) {
        alert('Your browser is not support cam.')
      }
      const gotMedia = (mediaStream) => {
        const mediaStreamTrack = mediaStream.getVideoTracks()[0]
        this.imageCapture = new ImageCapture(mediaStreamTrack)
        // eslint-disable-next-line
        console.log(this.imageCapture);
        if ("srcObject" in this.video) {
          this.video.srcObject = mediaStream
        } else {
          this.video.src = window.URL.createObjectURL(mediaStream);
        }
        this.video.onloadedmetadata = () => {
          this.video.play()
        };
      }
      if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        navigator.mediaDevices.getUserMedia(this.camContraints)
          .then(gotMedia)
          .catch(error => // eslint-disable-next-line
            console.error('getUserMedia() error:', error));
      }
    },
    async readImg (index) {
      // eslint-disable-next-line
      console.log(process.env)
      const ocrspac = this.axios.create({
        baseURL: 'https://api.ocr.space/',
        timeout: 25000,
        headers: {
          'Content-Type': 'multipart/form-data',
          'apiKey': process.env.VUE_APP_OCR_KEY
        }
      })
      const options = {
        language: 'eng',
        isOverlayRequired: false,
        base64Image: this.captures[index].image
      };
      const formData = new FormData();
      for (var key in options) {
        formData.append(key, options[key]);
      }
      let result = await ocrspac.post('parse/image', formData)
      // eslint-disable-next-line
      console.log(result)
      // const sendData = {
      //   image: {
      //     content: this.captures[index].image
      //       .replace('data:image/jpeg;base64,', '')
      //   },
      //   features: { type: 'LABEL_DETECTION', maxResults: 5 }
      // }
      // let { data } = await this.axios.post(
      //   // config.apiUrls.cloudVision,
      //   { requests: [sendData] }
      // )
      // if (data && data.responses[0].labelAnnotations) {
      //   this.imgReport = data.responses[0].labelAnnotations
      //   this.captures[index].imgReport = data.responses[0].labelAnnotations
      // }
    },
    async sendImgGVI (index) {
      const API_URL = `https://vision.googleapis.com/v1/images:annotate?key=${process.env.VUE_APP_OCR_GVA}`
      const sendData = {
        requests: [{
          image: {
            content: this.captures[index].image
              .replace('data:image/jpeg;base64,', '')
          },
          features: { type: 'DOCUMENT_TEXT_DETECTION' }
        }]
      }
      let { data } = await this.axios.post(
        API_URL,
        sendData
      )
      if (data && data.responses[0]) {
        this.imgReport = data.responses[0]
        this.captures[index].imgReport = data.responses[0]
      }
      // eslint-disable-next-line
      console.log(data)
    },
    capture () {
      this.canvas = this.$refs.canvas
      this.canvas.getContext('2d').drawImage(this.video, 0, 0, this.canvas.width, this.canvas.height)
      const URL = this.canvas.toDataURL('image/jpeg', 1)
      if (this.captures.length > 3) {
        this.captures.shift()
      }
      this.captures.push({
        image: URL,
        imgReport: {}
      })
      this.sendImgGVI(this.captures.length - 1)
    },
    gCapture () {
      this.imageCapture.takePhoto()
        .then(blob => {
          const reader = new FileReader()
          let dataURL = null
          reader.readAsDataURL(blob)
          reader.onloadend = () => {
            dataURL = reader.result;
            if (this.captures.length > 3) {
              this.captures.shift()
            }
            this.captures.push({
              image: dataURL,
              imgReport: {}
            })
            this.sendImgGVI(this.captures.length - 1)
          }
        })
    },
    fileSize (URL) {
      const stringLength = URL.length - 'data:image/png;base64,'.length;
      const sizeInBytes = 4 * Math.ceil((stringLength / 3)) * 0.5624896334383812
      const sizeInKb = sizeInBytes / 1000
      // eslint-disable-next-line
      console.log(sizeInKb)
      return sizeInKb
    }
  },
  filters: {
    percent: function (value) {
      if (!value) return ''
      return (Math.floor((value) * 10000) / 100).toFixed(2) + '%'
    }
  }
}
</script>

