<template>
  <div>
    <v-parallax src="https://cdn.vuetifyjs.com/images/parallax/material.jpg">
      <InputType v-on:toggleCam="toggleCam"></InputType>
    </v-parallax>
    <Cam v-if="openCam"></Cam>
  </div>
</template>

<script>
import InputType from './InputType.vue'
import Cam from './Cam.vue'

export default {
  name: 'home',
  data () {
    return {
      openCam: false
    }
  },
  methods: {
    toggleCam () {
      this.openCam = !this.openCam
    }
  },
  components: {
    InputType,
    Cam
  }
}
</script>
